import { Link } from 'preact-router/match';
import style from './style.css';

const Post = ({ post }) => (
  <div class={style.post}>
    <div class={style.postBody}>
    <p>{post.body}</p>
    <p class={style.attribution}>from <em>{post.title}</em> by <em>{post.author}</em></p>
    </div>
    <div class={style.images}>
      {post.media.map( img => (
        <img src={`/posts/${post.url}/${img}`}/>
      ))}
    </div>
  </div>
);

export default Post;
