import { Component, h } from 'preact';
import { Link } from 'preact-router/match';
import style from './style.css';

export default class Pagination extends Component {
  render(props) {
    var links = [];
    console.log("cc " + props.current);
    for(var i = 1; i <= props.pages; i++){ links.push(i) }
    return (
      <div className="pagination">
        {links.map(page => (
          <Link
            class={`${props.current == page ? style.active : style.inactive} ${style.paginationlink}`}
            href={`/page/${page}`}
          >
            {page}
          </Link>
        ))}
      </div>
    )
  }
}
