import { h, Component } from 'preact';
import style from './style';
import POSTS from '../../posts/posts.json';
import Post from '../../components/posts';
import Pagination from '../../components/pagination';


export default class Home extends Component {
	render({ pagenumber }) {
		var posts_to_display = [];
		var maxposts = 6;
		var start = 0;
		var fin = maxposts < POSTS.length ? maxposts : POSTS.length;
		var pageMessage = pagenumber ? `"${pagenumber}" isn't an available page number, we've given you the first page instead` : false;
		if(pagenumber >= 0 && pagenumber <= Math.ceil(POSTS.length/maxposts)){
			pageMessage = false;
			start = (pagenumber-1)*maxposts;
			fin = ((pagenumber-1)*maxposts)+maxposts;
			fin = fin < POSTS.length ? fin : POSTS.length;
			console.log(`fin:${fin} start:${start}`);
		}
		for(var i = start; i < fin; i++)
		{ posts_to_display.push(POSTS[i]); }
		console.log(posts_to_display)

		return (
					<div class={style.home}>
						{pageMessage ?
							(<h4>{pageMessage}</h4>) : ''
						}
						<div class="posts">
							{posts_to_display.map( posts => (
								<Post post={posts} />
							))}
						</div>
						{POSTS.length > maxposts ?
							(<Pagination pages={Math.ceil(POSTS.length/maxposts)} current={pagenumber} />) : ''
						}
					</div>
		)
	}
}
